import { ApiServer } from "../config/ApiServer";

const fetchServerStatus = async (server: ApiServer): Promise<boolean> => {
  const url = `${server.url}/health`
  let isAlive = false
  try {
    await fetch(url).then(() => isAlive = true)
  } catch (e) {
    console.log(e)
  }
  return isAlive
}

export const useServerStatus = () => fetchServerStatus
